import React from 'react';

class SlideShow extends React.PureComponent {
  constructor(props) {
    super(props);

    //! will need to be reset to 0 if new data is passed (different project),
    //! as the index may be out-of-range otherwise
    
    this.state = {
      index: 0, //* stores the current image index
    }; 

    //* switches between '' / active to refresh the css animation
    this.imgAnimClasses = '';

    //* called when user clicks the left/right slideshow arrow
    this.onNewImage = this.onNewImage.bind(this);
  }

  //* resets the image index if a different project is selected
  static getDerivedStateFromProps(props, state) {
    //* new project selected - the image index would be out of range
    if (state.index >= props.images.length) {
      return { index: 0 }; //* reset image index
    }

    return {}
  }

  onNewImage(e) {
    const { id } = e.currentTarget;
    let index = 0; //* new slideshow image index
    console.log(`onNewImage id: ${id}`);

    //* displays next image
    if (id === 'slideshow-next-btn') {
      if (this.state.index + 1 < this.props.images.length) { //* next images exist
        index = this.state.index + 1;
      }
    }

    //* displays previous image
    else {
      if (this.state.index - 1 >= 0) { //* previous images exist
        index = this.state.index - 1;
      }
      
      else { //* no previous images - looping to the back of the project images
        index = this.props.images.length - 1;
      }
    }

    //* toggles the image class so the css animation refreshes
    this.imgAnimClasses = (this.imgAnimClasses === '') ? 'active' : '';

    //* updates the image displayed 
    this.setState({ index });
  }


  render() {
    console.log('Slideshow rerendered');

    return (
      <div className='slideshow'>
        <button id='slideshow-prev-btn' className='slideshow-nav-btn' type='button' onClick={this.onNewImage}>
          <img src='./assets/placeholder/arrowLeft.svg' alt='navigate back' />
        </button>

        <button id='slideshow-next-btn' className='slideshow-nav-btn rotate-img' type='button' onClick={this.onNewImage}>
          <img src='./assets/placeholder/arrowLeft.svg' alt='navigate forward' />
        </button>

        <img className={`slideshow-project-img ${this.imgAnimClasses}`}
             src={this.props.images[this.state.index].path}
             alt={this.props.images[this.state.index].altText} />
      </div>
    )
  }
};

export default SlideShow;