
import React from 'react';
import ParticleLayer from './ParticleLayer';

const Home = function ({ homeRef }) {
  console.log('Home page re-rendered');

  return (
    <article id='home-page' className='main-page' ref={homeRef}>
      <ParticleLayer />

      <div className='logo-wrapper'>
        <img src='./assets/logo/logo-homepage.svg' alt='site logo' />
      </div>
      <div className='home-info' id='user-name'>Zach Niemann</div>
      <div className='home-info' id='user-title'>Full-stack developer</div>
    </article>
  );
};

export default React.memo(Home); 