import React from 'react';
import Slideshow from './Slideshow';

const ProjectCard = React.memo(({ info, onExit }) => {
  //* array of project descriptions are broken into individual paragraphs
  const displayDescriptions = info.description.map((text, index) => <p key={index} className='card-description'>{text}</p>);

  return (
    <div className='project-card'>
      <button className='exit-btn' type='button' onClick={onExit}>x</button>
      <Slideshow images={info.images} />

      <h3 className='card-title'>{info.title}</h3>
      <p className='card-tech-stack'>{info.techStack}</p>
      {displayDescriptions}
    </div>
  );
});

export default ProjectCard;