import React from 'react';

const ProjectSnippet = React.memo(({ title, techStack, onToggle }) => {
  //* appends the project title to determine the background image in css
  const projectClasses = `project-snippet ${title}`;

  //* determines which project is selected when "learn more" is clicked
  const btnID = `${title}-info`;

  return (
    <div className={projectClasses}>
      <div className='snippet-info'>
        <h3 className='snippet-info-title'>{title}</h3>
        <p className='snippet-info-tech-stack'>{techStack}</p>
        <button id={btnID} type='button' className='snippet-info-btn' onClick={onToggle}>learn more</button>
      </div>
    </div>
  );
});

export default ProjectSnippet;