
import React from 'react';
import PageHeader from '../PageHeader';
import ProjectSnippet from './ProjectSnippet';
import ProjectCard from './ProjectCard';


class Projects extends React.PureComponent {
  constructor(props) {
    super(props);

    //* if user clicks 'learn more' the project selected will have its
    //* data stored here. If no project is selected, the value is null
    this.state = { displayedModalInfo: null }; 

    //! description and images are used ONLY in the ProjectCard
    this.allProjects = [
      {
        title: 'Whisper',
        techStack: 'Electron | Node | React | Socket.IO',
        description: [
          'Whisper is the first chat app I coded using React!',
          'While private messaging and group chat work on the web, I also coded it as a desktop application using Electron (wraps the web abb for distribution on Windows/Mac/Linux.'
        ],
        images: [
          {
            path: './assets/projects/whisper-chatting.png',
            altText: 'chatting page'
          },
          {
            path: './assets/projects/whisper-sign-in.png',
            altText: 'sign in page'
          },
          {
            path: './assets/projects/whisper-sign-up.png',
            altText: 'sign up page'
          },
          {
            path: './assets/projects/whisper-landing.png',
            altText: 'landing page'
          }
        ]
      },
      {
        title: 'Testing',
        techStack: '^',
        description: ['2for modal window'],
        images: [
          {
            path: './assets/projects/whisper-sign-in.png',
            altText: 'sign in page'
          }
        ]
      }
    ];

    //* toggles the project modal display
    this.onModalToggle = this.onModalToggle.bind(this);  
  }


  onModalToggle(e) {
    const { id } = e.target;

    switch (id) {
      case 'Whisper-info':
        this.setState({ displayedModalInfo: this.allProjects[0] });
        break;
      
      case 'Testing-info':
        this.setState({ displayedModalInfo: this.allProjects[1] });
        break;
    
      default:
        this.setState({ displayedModalInfo: null });
    }
  }

  render() {
    console.log('Projects page re-rendered');

    return (
      <article id='projects-page' className='main-page' ref={this.props.projectsRef}>
        <PageHeader title='Projects' />

        <p className='page-info'>Lorem ipsum dolor sit amet, consectetur adipiscing elit,
        sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Eget est lorem
        ipsum dolor. Nulla facilisi nullam vehicula ipsum a arcu cursus vitae. Ac tortor
          dignissim convallis aenean et tortor at risus. A iacul
        </p>

        <div className='all-project-snippets'>
          <ProjectSnippet {...this.allProjects[0]} onToggle={this.onModalToggle} />
          <ProjectSnippet {...this.allProjects[1]} onToggle={this.onModalToggle} />
        </div>

        {this.state.displayedModalInfo && <ProjectCard info={this.state.displayedModalInfo}
                                                       onExit={this.onModalToggle} />}
      </article>
    );
  }
}


export default Projects; 
