import React from 'react';
import Navbar from './components/Navbar';
import View from './components/View';
import './main.css';


class App extends React.Component {
  constructor(props) {
    super(props);

    //* callback when user selects a page to navigate to on the navbar
    this.onPageSelect = this.onPageSelect.bind(this); 

    this.scrollBehavior = { behavior: 'smooth' };
    this.homeRef = React.createRef();
    this.aboutRef = React.createRef();
    this.projectsRef = React.createRef();
    this.contactRef = React.createRef();
  }

  onPageSelect(e) {
    e.preventDefault(); //* prevents immediately jumping to page
    const { id } = e.currentTarget; //* page id to navigate to

    //* navigate to homepage
    if (id === 'home-nav-btn' || id === 'mobile-home-link') {
      window.history.pushState(undefined, undefined, '#home-page');
      this.homeRef.current.scrollIntoView(this.scrollBehavior);
    }

    //* navigate to about page
    else if (id === 'about-link' || id === 'mobile-about-link') {
      window.history.pushState(undefined, undefined, '#about-page');
      this.aboutRef.current.scrollIntoView(this.scrollBehavior);
    }

    //* navigate to projects page
    else if (id === 'projects-link' || id === 'mobile-projects-link') {
      window.history.pushState(undefined, undefined, '#projects-page');
      this.projectsRef.current.scrollIntoView(this.scrollBehavior);
    }

    //* navigate to contact page
    else if (id === 'contact-link' || id === 'mobile-contact-link') {
      window.history.pushState(undefined, undefined, '#contact-page');
      this.contactRef.current.scrollIntoView(this.scrollBehavior);
    }
  }

  render() {
    return (
      <div className='App'>
        <Navbar onPageSelect={this.onPageSelect} />

        <View onPageSelect={this.onPageSelect} homeRef={this.homeRef} aboutRef={this.aboutRef} 
              projectsRef={this.projectsRef} contactRef={this.contactRef} />
      </div>
    )
  }
}
export default App;
