import React from 'react';
import { Defer } from 'react-progressive-loader';

const Header = ({ title }) => {
  return (
    <header>
      <h2 id={`${title.toLowerCase()}-header`} className='page-header'>{title}</h2>
    </header>
  );
}

const PageHeader = (props) => {
  return (
    <Defer
      render={() => <Header {...props} />}
      renderPlaceholder={() => <div>Loading...</div>}
      loadOnScreen
    />
  );
}

export default PageHeader;