import React, {PureComponent} from 'react';
import { CSSTransition } from 'react-transition-group';

const MobileMenu = function ({ onSelect }) {
  return (
    <div className='navbar-menu'>
      <a id='mobile-home-link' href='#home-page' className='navbar-link' onClick={onSelect}>home</a>
      <a id='mobile-about-link' href='#about-page' className='navbar-link' onClick={onSelect}>about</a>
      <a id='mobile-projects-link' href='#projects-page' className='navbar-link' onClick={onSelect}>projects</a>
      <a id='mobile-contact-link' href='#contact-page' className='navbar-link' onClick={onSelect}>contact</a>
    </div>
  );
};


class Navbar extends PureComponent {
  constructor(props) {
    super(props);

    //* toggles the mobile menu if clicked
    this.state = { isMobileMenuDisplayed: false };
    
    //* called when user clicks navbar menu (mobile view only)
    this.handleMenuToggle = this.handleMenuToggle.bind(this);

    //* called when user selects page from navbar menu (toggles menu off if active)
    this.handlePageSelection = this.handlePageSelection.bind(this);
  }

  handleMenuToggle(event) {
    this.setState((prevState) => ({
      isMobileMenuDisplayed: !prevState.isMobileMenuDisplayed
    }));
  }

  handlePageSelection(event) {
    if (this.state.isMobileMenuDisplayed) { //* only deactivates if menu is active
      this.setState({ isMobileMenuDisplayed: false });
    }

    this.props.onPageSelect(event); //* scrolls to page clicked
  }

  render() {
    return (
      <header>
        <nav>
          <div id='navbar-pages-wrapper'>
            <a id='about-link' href='#about-page' className='navbar-link' onClick={this.props.onPageSelect}>about</a>
            <a id='projects-link' href='#projects-page' className='navbar-link' onClick={this.props.onPageSelect}>projects</a>
            <a id='contact-link' href='#contact-page' className='navbar-link' onClick={this.props.onPageSelect}>contact</a>
          </div>

          <button id='navbar-mobile-menu-btn' type='button' onClick={this.handleMenuToggle}>
            <img className='navbar-icon' src='./assets/placeholder/mobileMenu.svg' alt='main menu'/>
          </button>

          <CSSTransition in={this.state.isMobileMenuDisplayed} timeout={800} classNames='mobile-menu'>
            <MobileMenu onSelect={this.handlePageSelection} /> 
          </CSSTransition>
        </nav> 
      </header>
    );
  }
}

export default Navbar;